import React, { Component } from 'react';
import { Container, Draggable } from 'react-smooth-dnd';
import { applyDrag, generateItems, stopEvent } from './utils';
import './styles.css';

class Simple extends Component {
  constructor() {
    super();
    this.state = {
      items: generateItems(50, (index) => {
        return {
          id: index,
          data: 'Draggable' + index
        };
      })
    };
  }
  render() {
    return (
      <div>
        <div className="simple-page">
          <Container onDrop={e => this.setState({ items: applyDrag(this.state.items, e) })}>
            {this.state.items.map(p => {
              return (
                <Draggable key={p.id}>
                  <div className="draggable-item">
                    {p.data}
                  </div>
                </Draggable>
              );
            })}
          </Container>
        </div>
      </div>
    );
  }
}

var containerElement =- null;


var scrollPosition = 0;

function preventDrag(e) {
    containerElement.scrollTop = scrollPosition;
};


class SimpleScroller extends Component {
    
    handleScroll = e => {
        this.setState({isDragging:true});
        e.preventDefault();
        // e.stopPropagation();
        console.log(e);
    }

    onDragStart = (a) => {
        console.log("onDragStart",a);
        // this._handleScroll = this.handleScroll.bind(this);
        // containerElement.addEventListener("scroll", this.handleScroll, {passive:false});
        scrollPosition = containerElement.scrollTop;
        containerElement.addEventListener("scroll", preventDrag, {passive:false});
        // window.addEventListener("scroll", this.handleScroll, {passive:false});
    }
    onDrop = e => {
        containerElement.removeEventListener("scroll", preventDrag, {passive:false});
        this.setState({ items: applyDrag(this.state.items, e)});
    }
    containerRef = (el) => {
        containerElement = el;
        console.log(containerElement);

    }
  constructor() {
    super();
    this.state = {
      items: generateItems(50, (index) => {
        return {
          id: index,
          data: 'Draggable' + index
        };
      })
    };
  }
  render() {
    console.log(this.state.isDragging);
    return (
      <div>
        <div ref={this.containerRef} className="simple-page-scroller" onContextMenu={stopEvent}>
          <Container onDragStart={this.onDragStart} onDrop={this.onDrop}>
            {this.state.items.map(p => {
              return (
                <Draggable key={p.id}>
                  <div className="draggable-item">
                    {p.data}
                  </div>
                </Draggable>
              );
            })}
          </Container>
        </div>
      </div>
    );
  }
}

export {
  Simple,
  SimpleScroller
};